import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { DocumentExtractService, ExtractRequest, DownloadedFile, ExtractResponse } from '../services/document-extract.service';

@Component({
  selector: 'app-extract-download',
  templateUrl: './extract-download.component.html',
  styleUrls: ['./extract-download.component.scss']
})
export class ExtractDownloadComponent implements OnInit {
  @Input() personId: number = 0;
  @Input() encntrId: number = 0;
  @Input() downloadedFiles: DownloadedFile[] = [];
  @Output() extractComplete = new EventEmitter<ExtractResponse>();

  isExtracting = false;
  extractProgress = 0;
  extractStatus = '';
  extractResult: ExtractResponse | null = null;

  constructor(private documentService: DocumentExtractService) { }

  ngOnInit(): void {
    console.log('[ExtractDownloadComponent] ngOnInit() - Component initialized with:', {
      personId: this.personId,
      encntrId: this.encntrId,
      downloadedFilesCount: this.downloadedFiles.length
    });
  }

  /**
   * TrackBy function for ngFor optimization
   * @param index The index of the item
   * @param file The file object
   * @returns The filename as the track key
   */
  trackByFilename(index: number, file: DownloadedFile): string {
    return file.filename;
  }

  /**
   * Starts the extract process to create metadata file and zip archive
   */
  startExtract(): void {
    console.log('[ExtractDownloadComponent] startExtract() - Starting extract process');
    
    if (this.downloadedFiles.length === 0) {
      console.warn('[ExtractDownloadComponent] startExtract() - No downloaded files available for extract');
      return;
    }

    this.isExtracting = true;
    this.extractProgress = 0;
    this.extractStatus = 'Preparing extract request...';
    this.extractResult = null;

    console.log('[ExtractDownloadComponent] startExtract() - Creating extract request with files:', this.downloadedFiles);

    // Create the extract request
    const extractRequest: ExtractRequest = {
      personId: this.personId,
      encntrId: this.encntrId,
      downloadedFiles: this.downloadedFiles
    };

    this.extractStatus = 'Sending extract request to server...';
    this.extractProgress = 25;

    this.documentService.createExtract(extractRequest).subscribe({
      next: (response: ExtractResponse) => {
        console.log('[ExtractDownloadComponent] startExtract() - Received extract response:', response);
        this.handleExtractResponse(response);
      },
      error: (error: any) => {
        console.error('[ExtractDownloadComponent] startExtract() - Extract error:', error);
        this.handleExtractError(error);
      }
    });
  }

  /**
   * Handles successful extract response
   * @param response The extract response from the service
   */
  private handleExtractResponse(response: ExtractResponse): void {
    console.log('[ExtractDownloadComponent] handleExtractResponse() - Processing extract response:', response);
    
    this.extractProgress = 100;
    this.extractStatus = response.success ? 'Extract completed successfully!' : 'Extract failed';
    this.extractResult = response;
    this.isExtracting = false;

    if (response.success) {
      console.log('[ExtractDownloadComponent] handleExtractResponse() - Extract successful, emitting result');
      this.extractComplete.emit(response);
    }
  }

  /**
   * Handles extract errors
   * @param error The error from the extract service
   */
  private handleExtractError(error: any): void {
    console.error('[ExtractDownloadComponent] handleExtractError() - Extract error:', error);
    
    this.extractProgress = 0;
    this.extractStatus = 'Extract failed';
    this.extractResult = {
      success: false,
      message: error.message || 'Extract process failed'
    };
    this.isExtracting = false;
  }

    /**
   * Downloads the created zip file with authentication
   */
  async downloadZipFile(): Promise<void> {
    if (this.extractResult?.zipFileUrl) {
      console.log('[ExtractDownloadComponent] downloadZipFile() - Downloading zip file:', this.extractResult.zipFileUrl);
      
      try {
        // Check if URL is HTTP and we're on HTTPS
        const isHttpsPage = window.location.protocol === 'https:';
        const isHttpUrl = this.extractResult.zipFileUrl.startsWith('http://');
        
        if (isHttpsPage && isHttpUrl) {
          console.warn('[ExtractDownloadComponent] downloadZipFile() - Mixed content detected, trying alternative methods');
          
          // Try to convert HTTP to HTTPS
          const httpsUrl = this.extractResult.zipFileUrl.replace('http://', 'https://');
          console.log('[ExtractDownloadComponent] downloadZipFile() - Trying HTTPS URL:', httpsUrl);
          
          // Test if HTTPS URL works
          const testResponse = await fetch(httpsUrl, { method: 'HEAD' });
          if (testResponse.ok) {
            console.log('[ExtractDownloadComponent] downloadZipFile() - HTTPS URL works, using it');
            this.downloadWithUrl(httpsUrl);
            return;
          }
        }
        
        // Fallback to original method
        this.downloadWithUrl(this.extractResult.zipFileUrl);
        
      } catch (error) {
        console.error('[ExtractDownloadComponent] downloadZipFile() - Download error:', error);
        
        // Show user-friendly error message
        alert('Download failed due to security restrictions. Please try the alternative download method or contact your administrator.');
      }
    }
  }

  /**
   * Helper method to perform the actual download
   */
  private downloadWithUrl(url: string): void {
    const link = document.createElement('a');
    link.href = url;
    
    // Use the descriptive filename from the extract response
    const downloadFilename = this.extractResult?.zipFileName || 'extracted_documents.zip';
    console.log('[ExtractDownloadComponent] downloadWithUrl() - Using filename for download:', downloadFilename);
    
    link.download = downloadFilename;
    link.target = '_blank';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  /**
   * Gets the extract button text based on current state
   * @returns Button text
   */
  getExtractButtonText(): string {
    if (this.isExtracting) {
      return 'Creating Extract...';
    }
    return 'Create Extract';
  }

  /**
   * Gets the extract status text for display
   * @returns Status text
   */
  getExtractStatusText(): string {
    if (this.isExtracting) {
      return `${this.extractStatus} (${this.extractProgress}%)`;
    }
    if (this.extractResult) {
      return this.extractResult.success ? 'Extract completed successfully!' : 'Extract failed';
    }
    return 'Ready to create extract';
  }

  /**
   * Gets the CSS class for the extract status
   * @returns CSS class name
   */
  getExtractStatusClass(): string {
    if (this.isExtracting) {
      return 'status-extracting';
    }
    if (this.extractResult) {
      return this.extractResult.success ? 'status-success' : 'status-failed';
    }
    return 'status-ready';
  }

  /**
   * Checks if the extract button should be disabled
   * @returns True if button should be disabled
   */
  isExtractButtonDisabled(): boolean {
    return this.isExtracting || this.downloadedFiles.length === 0;
  }

  /**
   * Checks if the download button should be shown
   * @returns True if download button should be shown
   */
  shouldShowDownloadButton(): boolean {
    return this.extractResult?.success === true && !!this.extractResult.zipFileUrl;
  }

  /**
   * Gets the download button text with filename
   * @returns Download button text
   */
  getDownloadButtonText(): string {
    if (this.extractResult?.zipFileName) {
      return ` Download ${this.extractResult.zipFileName}`;
    }
    return ' Download Zip File';
  }

  /**
   * Alternative download method using iframe to leverage existing session
   */
  downloadWithIframe(): void {
    if (this.extractResult?.zipFileUrl) {
      console.log('[ExtractDownloadComponent] downloadWithIframe() - Using iframe download method');
      
      try {
        // Check if URL is HTTP and we're on HTTPS
        const isHttpsPage = window.location.protocol === 'https:';
        const isHttpUrl = this.extractResult.zipFileUrl.startsWith('http://');
        
        let downloadUrl = this.extractResult.zipFileUrl;
        
        if (isHttpsPage && isHttpUrl) {
          console.warn('[ExtractDownloadComponent] downloadWithIframe() - Mixed content detected, trying HTTPS');
          // Try HTTPS version
          downloadUrl = this.extractResult.zipFileUrl.replace('http://', 'https://');
        }
        
        // Create an iframe to handle the download with existing session
        const iframe = document.createElement('iframe');
        iframe.style.display = 'none';
        iframe.src = downloadUrl;
        
        // Add to DOM temporarily
        document.body.appendChild(iframe);
        
        // Remove after a short delay
        setTimeout(() => {
          if (document.body.contains(iframe)) {
            document.body.removeChild(iframe);
          }
        }, 5000);
        
      } catch (error) {
        console.error('[ExtractDownloadComponent] downloadWithIframe() - Error:', error);
        alert('Alternative download method failed. Please contact your administrator.');
      }
    }
  }

  /**
   * Third download method using window.open for mixed content handling
   */
  downloadWithWindowOpen(): void {
    if (this.extractResult?.zipFileUrl) {
      console.log('[ExtractDownloadComponent] downloadWithWindowOpen() - Using window.open method');
      
      try {
        // Check if URL is HTTP and we're on HTTPS
        const isHttpsPage = window.location.protocol === 'https:';
        const isHttpUrl = this.extractResult.zipFileUrl.startsWith('http://');
        
        let downloadUrl = this.extractResult.zipFileUrl;
        
        if (isHttpsPage && isHttpUrl) {
          console.warn('[ExtractDownloadComponent] downloadWithWindowOpen() - Mixed content detected, trying HTTPS');
          // Try HTTPS version
          downloadUrl = this.extractResult.zipFileUrl.replace('http://', 'https://');
        }
        
        // Open in new window/tab
        const newWindow = window.open(downloadUrl, '_blank', 'noopener,noreferrer');
        
        if (!newWindow) {
          console.warn('[ExtractDownloadComponent] downloadWithWindowOpen() - Popup blocked, trying direct link');
          // Fallback to direct link
          this.downloadWithUrl(downloadUrl);
        }
        
      } catch (error) {
        console.error('[ExtractDownloadComponent] downloadWithWindowOpen() - Error:', error);
        alert('Window open method failed. Please try the other download methods.');
      }
    }
  }
} 